<template>
    <div class="user">
        <div class="actions" title="添加用户">
            <el-button @click="addUser" type="success" icon="el-icon-folder-add">添加用户</el-button>
        </div>
        <el-table class="full-width" :data="users" v-loading="loading" stripe border>
            <el-table-column
                    v-for="(column, index) in columns"
                    :key="index"
                    :prop="column.name"
                    :label="column.title"
                    :width="column.width"
                    :min-width="column.minWidth"
            >
                <template slot-scope="scope">
                    {{ column.format ? column.format(scope.row[column.name]) : (scope.row[column.name] === '' ? '未设置' :
                    scope.row[column.name]) }}
                </template>
            </el-table-column>
            <el-table-column label="操作" min-width="120" width="150">
                <template slot-scope="scope">
                    <el-button size="mini" @click="editUser(scope.row)" title="编辑" icon="el-icon-edit"/>
                    <el-button size="mini" type="danger" v-if="scope.row.role !== 'admin' && limit> 200 " title="删除" icon="el-icon-delete" @click="removeUser(scope.row)"/>
                </template>
            </el-table-column>
        </el-table>
        <div class="paging padding">
            <el-pagination background layout="prev, pager, next" :total="users.length" :current-page.sync="currentPage" :hide-on-single-page="true"/>
        </div>
        <el-dialog :title="isEdit ? '编辑用户' : '添加用户'" :visible.sync="addUserDialog" width="450px"
                   :modal-append-to-body="false" @closed="cancel">
            <el-form :model="user" :rules="rules" ref="form" label-width="100px">
                <el-form-item prop="name" label="用户名">
                    <el-input v-model="user.name" :disabled="user.role === 'admin' || limit <= 200" placeholder="请输入用户名"/>
                </el-form-item>
                <el-form-item prop="password" label="密码">
                    <el-input v-model="user.password" type="password" placeholder="请输入密码" show-password/>
                </el-form-item>
                <el-form-item prop="role" label="角色" v-if="!(isEdit && user.role == 'admin')">
                    <el-select v-model="user.role" placeholder="请选择" :disabled=" limit <= 200">
                        <el-option :label="'上传者'" :value="'uploader'"></el-option>
                        <el-option :label="'下载者'" :value="'downloader'"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item prop="phone" label="手机号">
                    <el-input v-model="user.phone" placeholder="请输入手机号"/>
                </el-form-item>
                <el-form-item prop="email" label="邮箱">
                    <el-input v-model="user.email" placeholder="请输入邮箱"/>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button size="small" @click="addUserDialog = false">取消</el-button>
                <el-button type="primary" size="small" @click="submit('form')">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as http from '@/libs/request'
    import {ObjectToFormdata} from '@/libs/formdata'
    import { formatDate } from '@/libs/moment'
    import { getLimit } from '@/libs/auth'
    export default {
        data: () => ({
            columns: [
                {
                    title: '用户名',
                    minWidth: 180,
                    name: 'name',
                },
                {
                    title: '角色',
                    name: 'role',
                    format: role => {
                        return {
                            admin: '管理员',
                            downloader: '下载者',
                            uploader: '上传者'
                        }[role]
                    },
                },
                {
                    title: '手机',
                    width: 180,
                    name: 'phone',
                },
                {
                    title: '邮箱',
                    width: 200,
                    name: 'email',
                },
                {
                    title: '创建时间',
                    width: 180,
                    name: 'created_at'
                },
                {
                    title: '更新时间',
                    width: 180,
                    name: 'updated_at'
                },
            ],
            users: [],
            currentPage: 1,
            loading: false,
            user: {
                name: '',
                password: '',
                phone: '',
                email: '',
                role: 'admin'
            },
            addUserDialog: false,
            rules: {
                name: [
                    {required: true, message: '用户名不能为空', trigger: 'blur'}
                ],
                password: [
                    {required: true, message: '密码不能为空', trigger: 'blur'}
                ],
            },
            isEdit: false,
            limit: getLimit()
        }),
        methods: {
            listUsers() {
                this.loading = true;
                http.listUser({
                    success: res => {
                        this.users = res.users ? res.users.map(({id, username, password, role, phone, mail, created_at, updated_at}) => {
                            return {
                                id,
                                name: username,
                                password,
                                role,
                                phone,
                                email: mail,
                                created_at:formatDate(created_at),
                                updated_at:formatDate(updated_at),
                            }
                        }) : [];
                        this.loading = false;
                    },
                    fail: err => {
                        this.$message.error('获取用户失败, ' + err);
                        this.loading = false;
                    }
                })
            },
            cancel() {
                this.addUserDialog = false;
                this.reset();
            },
            reset() {
                this.$refs.form.resetFields();
                this.user = {
                    name: '',
                    password: '',
                    phone: '',
                    email: ''
                }
            },
            addUser() {
                this.isEdit = false;
                this.addUserDialog = true;
            },
            editUser(row) {
                this.isEdit = true;
                this.user = {...row};
                this.addUserDialog = true;
            },
            submit(form) {
                this.$refs[form].validate(valid => {
                    if (!valid) {
                        return false;
                    } else {
                        this.saveUser()
                    }
                })
            },
            saveUser() {
                let {id, name, role, password, phone, email} = this.user;
                let formData = ObjectToFormdata({
                    username: name,
                    password,
                    phone,
                    mail: email,
                });
                let callback = {
                    success: res => {
                        this.addUserDialog = false;
                        this.listUsers()
                    },
                    fail: err => {
                        this.$message.error('操作失败, ' + err)
                    }
                };
                if (this.isEdit) {
                    formData.append('id', id);
                    formData.append('role', role);
                    http.updateUser(formData, callback)
                } else {
                    formData.append('role', role);
                    http.addUser(formData, callback)
                }
            },
            removeUser({id}) {
                this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteUser(id)
                })
            },
            deleteUser(id) {
                http.deleteUser({id}, {
                    success: res => {
                        this.listUsers()
                    },
                    fail: err => {
                        this.$message.error('获取用户失败, ' + err);
                    }
                })
            }
        },
        computed: {
            rows() {
                let limit = (this.currentPage - 1) * 10;
                return this.users.slice(limit, limit + 10);
            }
        },
        mounted() {
            this.listUsers()
        },
    }
</script>
